.item-panel {
  width: 190px;
  height: calc(100vh - 38px);
  min-width: 190px;
  overflow: hidden;
  background-color: #ffffff;
  border-right: 1px solid #e5e5e5;
}

.item-panel_title {
  padding: 5px;
  border-bottom: 1px solid #e5e5e5;
}

.item-panel__categories {
  padding: 5px;
}